import React from 'react';
import { Card } from 'pokemon-tcg-sdk-typescript/dist/sdk';

interface IProps {
    pokemon: Card;
};
interface IState {};

export default class Pokemon extends React.Component<IProps, IState> {
    render () {
        let types = ['normal', 'reverseHolofoil', 'holofoil', '1stEditionNormal', '1stEditionHolofoil'];
        let normalPrices = this.props.pokemon.tcgplayer?.prices?.normal;
        let reversePrices = this.props.pokemon.tcgplayer?.prices?.reverseHolofoil;
        let holofoilPrices = this.props.pokemon.tcgplayer?.prices?.holofoil;

        return (
            <div>
                <div>
                    <img src={this.props.pokemon.images.small}></img> {this.props.pokemon.name} {this.props.pokemon.rarity}
                </div>
                <div>
                    NORMAL: {JSON.stringify(normalPrices)}
                </div>
                <div>
                    REVERSE: {JSON.stringify(reversePrices)}
                </div>
                <div>
                    HOLOFOIL: {JSON.stringify(holofoilPrices)}
                </div>
            </div>
        );
    }
}